body {
  background-color: #9266ff;
}

.App {
  background-color: #3f3160;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
  border-radius: 10px;
}

header {
  margin-top: 20px;
}

#todo-form input {
  background-color: rgb(27, 112, 137);
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  border: none;
  border-radius: 10px;
  font-size: 18px;
  color: white;
}

#todo-form input::placeholder {
  color: (255, 255, 255, 0.5);
}

#todo-form input:focus, #todo-form button:focus {
  outline: none;
}

#todo-form button {
  background-color: rgb(238, 180, 20);
  padding: 10px;
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: none;
  color: white;
  cursor: pointer;
  font-weight: bolder;
}